	padding: 0.75rem 1.25rem;
	background-color: #f8f9fa;
	border-top: 1px solid rgba(17, 17, 17, 0.125);
}

.card-footer:last-child {
	border-radius: 0 0 calc(0 - 1px) calc(0 - 1px);
}

