	padding: 1.25rem;
}

.card-img-overlay-opacity {
	background: rgba(0, 0, 0, 0.2);
}

.card-img {
	border-radius: calc(0 - 1px);
}

.card-img-top {
	border-top-left-radius: calc(0 - 1px);
	border-top-right-radius: calc(0 - 1px);
}

.card-img-bottom {
	border-bottom-right-radius: calc(0 - 1px);
